<i18n>
{
  "en": {
    "bindingMobilePhone": "Binding mobile phone",
    "Unbound": "Unbound",
    "Send": "Send",
    "PhoneNumber": "Phone number",
    "VerificationCode": "Verification code",
    "changePhonenumber": "Change mobile phone number",
    "editorPhonenumber": "Confirm the modification of the mobile phone number",
    "boundDes": "Changing the mobile phone number does not affect the deposit of the current account, and other account information. after the replacement, you can log in using the new mobile phone number"
  },
  "zh": {
    "bindingMobilePhone": "绑定手机号",
    "Unbound": "立即绑定",
    "Send": "获取验证码",
    "PhoneNumber": "请输入手机号",
    "VerificationCode": "请输入验证码",
    "changePhonenumber": "更换手机号",
    "editorPhonenumber": "确认修改手机号",
    "boundDes": "更换手机号不影响当前账户的押金、等账户信息，更换后，可以使用新手机号登录"
  }
}
</i18n>

<template>
  <div class="bindingPhone">
    <head-top :title='$t(title)' :go-back="true"></head-top>
    <div class="binding-icon">
      <img src="../../assets/sidebar_images/synermax@3x.png" alt="" v-if="Version === 1"/>
      <img src="../../assets/synerMax-skin/synermax@3x.png" alt="" v-if="Version === 2"/>
      <img src="../../assets/sidebar_images/connect@3x.png" alt="">
      <img src="../../assets/sidebar_images/phone@3x.png" alt="" v-if="Version === 1"/>
      <img src="../../assets/synerMax-skin/phone1@3x.png" alt="" v-if="Version === 2"/>
    </div>
    <div class="input">
      <div class="phone">
        <input
        type="number"
        :placeholder="$t('PhoneNumber')"
        oninput="if(value.length>11)value=value.slice(0,11)"
        @input="phoneVolidate"
        v-model="phone"
        >
      </div>
      <div class="code">
        <input
        type="number"
        :placeholder="$t('VerificationCode')"
        oninput="if(value.length>6)value=value.slice(0,6)"
        v-model="code"
        @input="codeVolidate"
        >
        <button @click="getCode" :class="{active : getCoded}">
          <em v-if="disGetcode">{{$t('Send')}}</em>
          <em v-else>{{this.codeCountDown + 's'}}</em>
        </button>
      </div>
    </div>
    <button class="binding" :class="{active : buttoned}" @click="binding">{{$t(bindType)}}</button>
    <span class="bttx">{{$t('boundDes')}}</span>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'bindingPhone',
  components: {
    headTop
  },
  data () {
    return {
      phone: null,
      code: null,
      getCoded: false,
      buttoned: false,
      disGetcode: true,
      codeCountDown: 59,
      title: 'bindingMobilePhone',
      bindType: 'Unbound'
    }
  },
  mounted () {
    if (this.$route.params.bindType === 'bind') {
      this.title = 'bindingMobilePhone'
      this.bindType = 'Unbound'
    } else {
      this.title = 'changePhonenumber'
      this.bindType = 'editorPhonenumber'
    }
    this.dataInteractionResponse(data => {
      switch (data.msgType) {
        case '0x5106': // 获取验证码返回
          if (data.result) {
            this.disGetcode = false
            let timer = setInterval(() => {
              if (this.codeCountDown <= 0) {
                this.disGetcode = true
                this.codeCountDown = 59
                clearInterval(timer)
              }
              this.codeCountDown--
            }, 1000)
          }
          break
        case '0x5163':
          if (data.result) {
            if (data.data.binding) {
              this.$router.go(-1)
              this.$store.state.user.bindPhone = true
            }
          }
          break
        default:
          break
      }
    })
  },
  methods: {
    getCode () {
      if (!this.getCoded) return false
      this.dataInteractionRequest({
        msgType: '0x5105',
        phone: this.phone,
        type: 5
      })
      if (this.ISDEBUG) {
        let str = '{"msgType":"0x5106","result":true,"data":{"code":123456}}'
        window.dataInteractionResponse(str)
      }
    },
    binding () {
      if (!this.buttoned) return false
      this.dataInteractionRequest({
        msgType: '0x5162',
        phone: this.phone,
        code: this.code
      })
      if (this.ISDEBUG) {
        let str = '{"msgType":"0x5163","result":true,"data":{"binding":true}}'
        window.dataInteractionResponse(str)
      }
    },
    phoneVolidate () {
      if (/^1[3456789]\d{9}$/.test(this.phone)) {
        this.getCoded = true
      } else {
        this.getCoded = false
      }
    },
    codeVolidate () {
      if (this.code.length >= 6 && /^1[3456789]\d{9}$/.test(this.phone)) {
        this.buttoned = true
      } else {
        this.buttoned = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.bindingPhone{
  .fs;
  background: #FFF;
  .binding-icon{
    .wh(100%, 280px);
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    img:nth-child(1), img:nth-child(3){
      width: 120px;
    }
    img:nth-child(2){
      width: 70px;
      margin: 0 40px;
    }
  }
  .input{
    .wh(100%, 240px);
    display: flex;
    flex-direction: column;
    text-align: left;
    padding: 20px 50px;
    .phone{
      flex: 1;
      input{
        .wh(100%, 100%);
        border-bottom: 1px solid #f7f7f7;
      }
    }
    .code{
      flex: 1;
      margin-top: 20px;
      input{
        .wh(60%, 100%);
        border-bottom: 1px solid #f7f7f7;
      }
      button{
        float: right;
        .wh-l(200px, 80px);
        background-color: #EEE;
        border-radius: 10px;
        color: #A6A6A6;
      }
      .active{
        background: @themeColor;
      }
    }
  }
  .bttx{
    font-size: 26px;
    color: #585858;
    display: flex;
    padding: 0 30px;
    text-align: left;
  }
  .binding{
    .wh(80%, 90px);
    border-radius: 10px;
    margin: 60px auto;
    background-color: #EEE;
    color: #A6A6A6;
  }
  .active{
    background: @themeColor;
  }
}
</style>



